.containerCard {
    display: flex;
    height: 200px;
    margin-left: 140px;
    position: relative;
}
.containerCard:not(:first-child) {
    margin-top: 30px;
}
.containerCard::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 75%;
    height: 2px; /* Высота черты */
    background-color: #E0E0E0; /* Цвет черты, измените по желанию */
}

.photoCard img {
    width: 246px;
    height: 180px;
    border-radius: 6px;
}
.read {
    display: block;
}
.tlt {
    margin-left: 18px;
    color: #21243D;
    font-family: Heebo;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.blockOval {
    margin-top: 20px;
    margin-left: 18px;
    width: 62px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    border-radius: 16px;
    background: #142850;

    color: #FFF;

    font-family: Heebo;
    font-size: 18px;
    font-style: normal;
    font-weight: 900;
    line-height: 60px;
}
.txt {
    margin-top: 22px;
    margin-left: 18px;
    height: 93px;
    width: 60%;
    color: #21243D;
    font-family: Heebo;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    overflow: hidden;
}

@media (max-width: 768px) {
    .containerCard {
        margin-left: 20px;
        flex-direction: column;
        height: auto;
        margin-right: 20px;
    }
    .containerCard::after {
        width: 100%;
    }
    .photoCard img {
        width: 100%;
        height: 200px;
    }
    .tlt {
        margin-top: 10px;
        margin-left: 0;
    }
    .blockOval {
        margin-left: 0;
    }
    .txt {
        margin-left: 0;
        width: 100%;
        height: auto;
        margin-bottom: 10px;
    }
}
