.footerContainer {
    display: flex;
    height: 182px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contact {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.contact img {
    margin-bottom: 35px;
}
.contact img:not(:last-child) {
    margin-right: 30px;
}

@media (max-width: 768px) {
    .footerContainer {
        margin-top: 20px;
        height: 100%;
        margin-bottom: 20px;
    }
