.worksMainContainer {
    height: 390px;
    background: #EDF7FA;
}
.worksTitle {
    margin-left: 140px;
    color:  #21243D;
    font-family: Heebo;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 60px; /* 272.727% */
    margin-top: 7px;
}
.postsList {
    display: flex;
    margin-left: 140px;

}

@media (max-width: 768px) {
    .worksMainContainer {
        height: 100%;
    }
    .postsList {
        margin-left: 20px;
        margin-right: 20px;
    }
    .postsList {
        flex-direction: column;
    }
}
